
<el-dialog
	title="设置权限"
	:visible="show"
	:show-close='false'
	width="350px">
	<el-tree
	ref="powerTree"
	:data="routerPower"
	show-checkbox
	node-key="name"
	default-expand-all
	@check="powerCheck"
	:default-checked-keys="powerData"
	:props="defaultProps">
	</el-tree>
	<span slot="footer">
		<el-button @click="cancel">取消</el-button>
		<el-button type="primary" @click="save" :disabled="equally">确定</el-button>
	</span>
</el-dialog>
